body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

html {
  font-size: 16px;
  font-family: 'Raleway', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  position: relative;
}

.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

header {
  position: relative;
}

header nav {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  width: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000000;
}

header nav .content {
  display: flex;
  justify-content: space-between;
  padding-top: 43px;
}

header nav .content .logo {
  width: calc(16.666666666666668% - 30px);
}

header nav .content .contact {
  width: calc(16.666666666666668% - 30px);
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

header nav .content .contact a {
  font-weight: 900;
  font-size: 17px;
  text-decoration: none;
  color: #fff;
  margin-bottom: 10px;
}

header nav .content .contact span {
  font-weight: 700;
  color: #fff;
  font-size: 11px;
  text-transform: uppercase;
}

header .slide-one {
  width: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("..\images\header\slide-one\bg-header.jpg") no-repeat top center / cover;
}

header .slide-one .content {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 214px;
  text-align: left;
  position: relative;
}

header .slide-one .content::before {
  display: block;
  content: url("..\images\header\slide-one\navigation.png");
  position: absolute;
  top: 50%;
  right: 0;
}

header .slide-one .content b {
  display: inline-block;
  font-size: 21px;
  font-weight: 900;
  color: #10e08b;
  margin-bottom: 18px;
}

header .slide-one .content h1 {
  font-size: 56px;
  font-weight: 900;
  color: #fff;
  margin-bottom: 129px;
  line-height: 65px;
}

header .slide-one .content ul {
  display: flex;
}

header .slide-one .content ul li {
  width: calc(25% - 30px);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  color: #fff;
  font-size: 15px;
  font-weight: 700;
}

header .slide-one .content ul li hr {
  margin-top: 15px;
}

header .slide-one .content .button {
  display: flex;
  justify-content: flex-end;
  margin-top: 78px;
}

header .slide-one .content .button button {
  padding: 25px 50px 25px 80px;
  background: #10e08b;
  border: none;
  font-weight: 900;
  font-size: 16px;
  color: #fff;
  position: relative;
}

header .slide-one .content .button button::before {
  position: absolute;
  display: block;
  content: url("..\images\header\slide-one\arrow-for-button.png");
  left: 30px;
}

header .slide-two {
  width: 100%;
  display: flex;
  position: relative;
}

header .slide-two::before {
  display: block;
  content: url("..\images\header\slide-two\nav.png");
  position: absolute;
  top: 50%;
  right: 3%;
}

header .slide-two .left-block {
  width: calc(50% - 30px);
  background: url("..\images\header\slide-two\bg.jpg") no-repeat top center / cover;
  position: relative;
}

header .slide-two .left-block .content {
  padding-top: 333px;
}

header .slide-two .left-block .content img {
  margin: 0 auto;
  display: block;
  cursor: pointer;
}

header .slide-two .left-block span {
  display: block;
  text-align: center;
  margin-top: 20px;
  font-size: 15px;
  font-weight: 600;
  color: #fff;
  padding-bottom: 350px;
}

header .slide-two .right-block {
  width: calc(45.833333333333336% - 30px);
  margin: 0 auto;
  position: relative;
}

header .slide-two .right-block .content {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  text-align: left;
}

header .slide-two .right-block .content h2 {
  font-size: 48px;
  line-height: 56px;
  font-weight: 900;
  color: #000;
  margin-top: 85px;
}

header .slide-two .right-block .content p {
  font-size: 21px;
  line-height: 34px;
  color: #000;
  margin-top: 43px;
  margin-bottom: 97px;
}

header .slide-two .right-block .content ul {
  display: flex;
}

header .slide-two .right-block .content ul li {
  width: calc(83.33333333333334% - 30px);
  text-align: center;
  background: #f2f2f2;
  padding: 31px 55px;
}

header .slide-two .right-block .content ul li:nth-child(2) {
  margin-left: 20px;
}

header .slide-two .right-block .content ul li .image {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 132px;
}

header .slide-two .right-block .content ul li .image span {
  display: inline-block;
  margin-top: auto;
  line-height: 21px;
}

main {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  margin-top: 80px;
}

main .how-it-was {
  margin-bottom: 150px;
  overflow: hidden;
}

main .how-it-was .mobile {
  display: none;
  border-bottom: 1px solid #10e08b;
  border-top: none;
  margin-top: 15px;
  margin-bottom: 15px;
}

main .how-it-was h2 {
  text-align: center;
  font-size: 48px;
  line-height: 56px;
  font-weight: 900;
  margin-bottom: 80px;
}

main .how-it-was .content {
  display: flex;
  align-items: center;
}

main .how-it-was .content .slide-block {
  width: calc(50% - 30px);
  position: relative;
  margin-bottom: 60px;
}

main .how-it-was .content .slide-block p.date {
  position: absolute;
  top: 5%;
  left: 0;
  transform: rotate(270deg);
  font-size: 12px;
  color: #b3b3b3;
}

main .how-it-was .content .slide-block p.date span {
  font-weight: 900;
}

main .how-it-was .content .slide-block .mini-slide {
  width: calc(83.33333333333334% - 30px);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}

main .how-it-was .content .slide-block .mini-slide .image {
  position: relative;
}

main .how-it-was .content .slide-block .mini-slide .image.top::before {
  display: block;
  content: 'ВОСХОЖДЕНИЕ';
  position: absolute;
  bottom: 50px;
  left: 100%;
  transform: rotate(270deg) translateY(-50%);
  font-size: 12px;
  color: #b3b3b3;
  font-weight: 900;
  text-transform: uppercase;
}

main .how-it-was .content .slide-block .mini-slide .image.bot::after {
  display: block;
  content: 'Погружение';
  position: absolute;
  bottom: 43px;
  left: 100%;
  transform: rotate(270deg) translateY(-50%);
  font-size: 12px;
  color: #b3b3b3;
  font-weight: 900;
  text-transform: uppercase;
}

main .how-it-was .content .slide-block .mini-slide .image .buttons {
  position: absolute;
  background: #fff;
  top: 0;
  right: 0;
  transform: translateX(50%);
}

main .how-it-was .content .slide-block .mini-slide .image .buttons button {
  background: transparent;
  border: none;
  font-size: 40px;
  line-height: .5;
  padding: 25px 15px;
  font-weight: 500;
}

main .how-it-was .content .slide-block .mini-slide p {
  text-align: center;
  margin-top: 20px;
  font-size: 18px;
  line-height: 26px;
}

main .how-it-was .content .slide-block .mini-slide p span {
  color: #10e08b;
}

main .how-it-was .content .text-block {
  width: calc(33.333333333333336% - 30px);
  margin: 0 auto;
  text-align: center;
}

main .how-it-was .content .text-block h4 {
  font-size: 32px;
  line-height: 40px;
  color: #999999;
  font-weight: 200;
}

main .how-it-was .content .text-block h4 span {
  color: #10e08b;
}

footer {
  background: #303030;
}

footer .content {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

footer .content ul {
  display: flex;
  justify-content: space-evenly;
  padding-top: 80px;
  padding-bottom: 95px;
}

footer .content ul li {
  width: calc(33.333333333333336% - 30px);
}

footer .content ul li span {
  font-size: 12px;
  color: #fff;
  line-height: 24px;
}

footer .content ul li span.bold {
  font-weight: 900;
}

footer .content ul li p {
  font-size: 21px;
  color: #fff;
  margin-top: 15px;
  font-weight: 200;
}

.menu {
  width: calc(8.333333333333334% - 30px);
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100000000;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.menu nav {
  position: relative;
}

.menu nav .for-button {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu nav .for-button button {
  background: transparent;
  border: none;
  font-size: 30px;
  font-weight: 900;
}

.menu nav .for-button button:focus {
  outline: none;
  background: #10e08b;
  border-radius: 5px;
  color: #fff;
}

.menu nav .for-button button#close {
  display: none;
}

.menu nav .for-button span {
  font-size: 11px;
  font-weight: 900;
  text-transform: uppercase;
}

.menu nav ul {
  text-align: center;
  margin-top: 30px;
  display: none;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
}

.menu nav ul li {
  padding: 25px 1px;
  transform: rotate(-35deg);
}

.menu nav ul li a {
  text-decoration: none;
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
  transition: 1s;
}

.menu nav ul li a:hover {
  transition: 1s;
  color: #10e08b;
}

.menu .social {
  padding-top: 340px;
}

.menu .social ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  min-height: 243px;
}

.menu .social ul li {
  padding-left: 25px;
  transform: rotate(270deg);
}

.menu .social ul li.ins::before {
  display: block;
  content: url("..\images\menu\insta.png");
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(90deg);
}

.menu .social ul li.vk::before {
  display: block;
  content: url("..\images\menu\vk.png");
  position: absolute;
  top: -5px;
  left: -6px;
  transform: rotate(90deg);
}

.menu .social ul li a {
  text-decoration: none;
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  transition: 1s;
}

.menu .social ul li a:hover {
  transition: 1s;
  color: #10e08b;
}

@media screen and (max-width: 1350px) {
  header nav {
    width: 80%;
  }

  header nav .content .contact {
    width: calc(100% - 30px);
  }

  header .slide-one .content {
    width: calc(91.66666666666667% - 30px);
  }

  main .how-it-was .content .slide-block p.date {
    left: 5%;
  }
}

@media screen and (max-width: 992px) {
  header .slide-one .content {
    width: calc(66.66666666666667% - 30px);
  }

  header .slide-one .content h1 {
    font-size: 35px;
    margin-bottom: 25px;
    line-height: 40px;
  }

  header .slide-one .content h1 {
    font-size: 25px;
    margin-bottom: 40px;
    line-height: 25px;
  }

  header .slide-one .content ul {
    flex-direction: column;
  }

  header .slide-one .content ul li {
    width: calc(100% - 30px);
  }

  header .slide-one .content .button button {
    padding: 12px 25px 18px 51px;
    font-size: 14px;
  }

  header .slide-two::before {
    top: 40%;
  }

  header .slide-two .right-block .content h2 {
    font-size: 35px;
    line-height: 40px;
  }

  header .slide-two .right-block .content p {
    font-size: 17px;
    line-height: 22px;
    margin-bottom: 55px;
  }

  header .slide-two .right-block .content ul {
    flex-direction: column;
  }

  header .slide-two .right-block .content ul li:nth-child(2) {
    margin-left: 0;
    margin-top: 10px;
  }

  main .how-it-was .content .slide-block {
    width: calc(66.66666666666667% - 30px);
  }

  main .how-it-was .content .text-block h4 {
    font-size: 23px;
    line-height: 23px;
  }
}

@media screen and (max-width: 767px) {
  header nav .content .logo {
    width: 100%;
  }

  header .slide-one .content {
    width: calc(83.33333333333334% - 30px);
  }

  header .slide-one .content .button {
    justify-content: center;
  }

  header .slide-two {
    flex-direction: column;
  }

  header .slide-two .left-block {
    width: 100%;
  }

  header .slide-two .right-block {
    width: calc(83.33333333333334% - 30px);
  }

  header .slide-two .right-block .content p {
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 40px;
  }

  main .how-it-was .mobile {
    display: block;
  }

  main .how-it-was h2 {
    font-size: 35px;
    line-height: 35px;
    margin-bottom: 50px;
  }

  main .how-it-was .content {
    flex-direction: column;
  }

  main .how-it-was .content .slide-block {
    width: calc(91.66666666666667% - 30px);
  }

  main .how-it-was .content .slide-block {
    margin-bottom: 40px;
    order: -1;
  }

  main .how-it-was .content .text-block {
    width: calc(91.66666666666667% - 30px);
  }

  main .how-it-was .content .text-block h4 {
    font-size: 45px;
    line-height: 35px;
  }
}

@media screen and (max-width: 576px) {
  header nav .content .contact {
    width: 100%;
  }

  header nav .content .contact a {
    font-size: 15px;
  }

  header .slide-one .content::before {
    display: none;
  }

  header .slide-two .right-block {
    width: calc(91.66666666666667% - 30px);
  }

  header .slide-two .right-block .content h2 {
    font-size: 30px;
    line-height: 30px;
    margin-top: 40px;
  }

  main .how-it-was .content .slide-block p.date {
    left: 0;
  }

  main .how-it-was .content .slide-block .mini-slide .image.top::before {
    left: 97%;
  }

  main .how-it-was .content .slide-block .mini-slide .image .buttons button {
    padding: 17px 8px;
  }

  main .how-it-was .content .text-block h4 {
    font-size: 25px;
    line-height: 25px;
  }

  main .how-it-was .content .text-block h4 br {
    display: none;
  }

  footer .content ul {
    flex-direction: column;
  }

  footer .content ul li {
    width: calc(100% - 30px);
  }

  footer .content ul li {
    text-align: center;
    margin-bottom: 15px;
  }
}

@media screen and (max-width: 320px) {
  header nav .content .logo {
    width: 35%;
  }

  header .slide-one .content {
    width: calc(100% - 30px);
  }

  header .slide-two .right-block .content h2 {
    font-size: 20px;
    line-height: 23px;
    margin-top: 30px;
  }

  header .slide-two .right-block .content p {
    font-size: 12px;
    line-height: 13px;
    margin-top: 30px;
    margin-bottom: 30px;
  }

  header .slide-two .right-block .content ul li {
    padding: 31px 35px;
  }

  main .how-it-was .content .slide-block {
    width: calc(100% - 30px);
  }
}